<template>
  <el-container class="page">
    <el-header>
      <div class="header"></div>
    </el-header>
    <el-container>
      <el-aside class="nav" width="200px">
        <div class="admin-info">
          <div class="admin-img">
            <img src="../../assets/doc.png" alt>
          </div>
          <div class="admin-text">
            <div>张医生</div>
            <div style="font-size: 13px;">呼吸内科1诊室</div>
          </div>
        </div>
        <el-menu
          class="el-menu-vertical-demo"
          background-color="#2771D9"
          text-color="#fff"
          active-text-color="#fff"
        >
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-document"></i>
              <span slot="title">转诊管理</span>
            </template>
            <router-link to="/newreferral">
              <el-menu-item index="1-1">
                <i class="el-icon-menu"></i>
                <span slot="title">新建转诊</span>
              </el-menu-item>
            </router-link>
            <router-link to="/referralrecord">
              <el-menu-item index="1-2">
                <i class="el-icon-menu"></i>
                <span slot="title">转诊记录</span>
              </el-menu-item>
            </router-link>
          </el-submenu>

          <el-submenu index="2">
            <template slot="title">
              <i class="el-icon-setting"></i>
              <span slot="title">统计分析</span>
            </template>
            <router-link to="/referraltotal">
              <el-menu-item index="2-1">
                <i class="el-icon-menu"></i>
                <span slot="title">转诊总量</span>
              </el-menu-item>
            </router-link>
            <router-link to="/referraltrend">
              <el-menu-item index="2-2">
                <i class="el-icon-menu"></i>
                <span slot="title">转诊趋势</span>
              </el-menu-item>
            </router-link>
            <router-link to="/inhospitalcost">
              <el-menu-item index="2-3">
                <i class="el-icon-menu"></i>
                <span slot="title">住院费用</span>
              </el-menu-item>
            </router-link>
          </el-submenu>
        </el-menu>
      </el-aside>

      <el-container class="right">
        <el-main>
          <router-view></router-view>
        </el-main>
        <el-footer>@东软大象</el-footer>
      </el-container>
    </el-container>
  </el-container>
</template>
<style>
html,body{
  overflow: auto;
}
.page {
  height: 100vh;
}
.nav {
  background-color: rgb(39, 113, 217);
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.header {
  background: url("../../assets/logo.png") no-repeat;
  height: 60px;
  background-position: 16px 8px;
  background-size: auto 44px;
}

a:link {
  color: #fff;
  text-decoration: none;
}
a:active {
  color: #fff;
  text-decoration: none;
}
a:visited {
  color: #fff;
  text-decoration: none;
}
a:hover {
  color: #fff;
  text-decoration: none;
}

.right {
  background: #edf4f9;
}
.admin-info {
  display: flex;
  align-items: center;
  justify-items: center;
  padding: 20px 0;
}
.admin-img {
  width: 80px;
  text-align: center;
  min-width: 80px;
}
.admin-img img {
  vertical-align: middle;
  width: 60px;
  height: 60px;
}
.admin-text {
  text-align: left;
  color: #fff;
}
.el-submenu__title {
  text-align: left;
}
.el-menu {
  border: none;
}
.el-menu-item {
  padding-left: 0px !important;
}
.el-menu-item i {
  color: #fff;
}
.el-submenu__title i {
  color: #fff;
}
.el-menu-item.is-active {
  background-color: rgb(31, 90, 174) !important;
}
</style>
<script>
export default {
  data() {
    return {
      isCollapse: true
    };
  },
  methods: {}
};
</script>




